import { Pengumuman, Kantor, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { Component, OnInit } from '@angular/core';
import { CompanyPengumumanService, CompanyDivisiService, CompanyDepartemenService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';



@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private companyPengumumanService: CompanyPengumumanService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private companyDepartemenService: CompanyDepartemenService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }



    dataPengumuman: Pengumuman = {}
    dataKantor: Kantor = {}
    dataDepartemen: Departemen = {}
    dataDivisi: Divisi = {}


    id_pengumuman: string = ''

    ngOnInit() {
        this.id_pengumuman = this.routerActive.snapshot.paramMap.get('id_pengumuman')
        console.log(this.id_pengumuman)
        this.getPengumuman();
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getPengumuman();
                    this.getLog()
                }
            }
        )

    }

    kantor: any = ''
    params: standarFlow = null;
    id_data_flow: string = ''
    getPengumuman() {
        this.spinner.show()
        this.companyPengumumanService.getCompanyPengumumenId(this.id_pengumuman).subscribe(
            response => {
                let xx = response.data;
                xx.id_kantor = JSON.parse(xx.id_kantor)
                this.dataPengumuman = xx
                this.kantor = xx.id_kantor
                // this.getDivisi(this.dataPengumuman.id_divisi)
                // this.getDepartemen(this.dataPengumuman.id_departemen)
                // console.log(xx.id_kantor)


                this.params = {
                    id: response.data.id_pengumuman,
                    id_data_flow_detail: response.data.id_data_flow_detail,
                    id_data_flow: response.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_pengumuman"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )

    }

    getDivisi(id_divisi) {
        this.companyDivisiService.getCompanyDivisisId(id_divisi).subscribe(
            data => {
                this.dataDivisi = data.data
            }
        )
    }
    getDepartemen(id_departemen) {
        this.companyDepartemenService.getCompanyDepartemensId(id_departemen).subscribe(
            data => {
                this.dataDepartemen = data.data
            }
        )
    }


    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_pengumuman })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {

        this.router.navigate(['/hr/company/module/pengumuman/list'])
    }

}
