import { Pengumuman } from 'src/app/sdk/hr/models/pengumuman';
import { Kantor, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { CompanyPengumumanService, CompanyKantorService, CompanyDepartemenService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';



@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyKantorService: CompanyKantorService,
        private companyPengumumanService: CompanyPengumumanService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private router: Router
    ) { }

    data: Pengumuman = {};
    ngOnInit() {
        this.form();
        this.getPreeData()
        this.getDepartement()
    }


    listDepartemen: Departemen[] = [];
    listKantor: Kantor[] = [];
    listDivisi: Divisi[] = [];
    getPreeData() {
        this.listKantor = []
        this.companyKantorService.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.id_departemen,
                status: 'Active'
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getDepartement() {
        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )
    }



    dataForm: FormGroupTypeSafe<Pengumuman>;
    form() {
        this.dataForm = this.fb.group<Pengumuman>(
            {
                id_departemen: new FormControl(''),
                judul: new FormControl('', [Validators.required]),
                id_kantor: new FormControl(''),
                id_divisi: new FormControl(''),
                tanggal_berakhir: new FormControl(''),
                tanggal_mulai_berlaku: new FormControl('', [Validators.required]),
                by: new FormControl(''),
                nomor: new FormControl(''),

            }
        )
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }


        this.data = this.dataForm.value;
        this.companyPengumumanService.postCompanyPengumumen(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/pengumuman/list'])
    }
}
