import { SingleUploadService } from './../../../../../components/document/single-upload.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyDepartemenService, CompanyKantorService, CompanyPengumumanService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Pengumuman, Departemen, Kantor, Divisi } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyKantorService: CompanyKantorService,
        private companyPengumumanService: CompanyPengumumanService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    id_pengumuman: string = ''
    data: Pengumuman = {};
    toogleUpload = false
    ngOnInit() {
        this.id_pengumuman = this.activatedRoute.snapshot.paramMap.get('id_pengumuman')
        this.form();
        this.getPreeData()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company_announcement.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }


    listDepartemen: Departemen[] = [];
    listKantor: Kantor[] = [];
    listDivisi: Divisi[] = [];
    getPreeData() {
        this.listKantor=[]
        this.companyKantorService.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.id_departemen,
                status: 'Active'
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getDepartement(){
        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )
    }

    getData() {
        this.spinner.show()
        this.companyPengumumanService.getCompanyPengumumenId(this.id_pengumuman).subscribe(
            data=> {
                let xx= data.data;
                xx.id_kantor = JSON.parse(xx.id_kantor)
                this.dataForm.setValue(xx)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "company_pengumuman",
                    for_table: "erp.pengumuman",
                    for_module_id: data.data.id_pengumuman
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pengumuman>;
    form() {
        this.dataForm = this.fb.group<Pengumuman>(
            {
                id_departemen: new FormControl('', ),
                judul: new FormControl('', [Validators.required]),
                id_kantor: new FormControl('', ),
                id_divisi: new FormControl('', ),
                tanggal_berakhir: new FormControl('', [Validators.required]),
                tanggal_mulai_berlaku: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_pegawai: new FormControl(''),
                id_pengumuman: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                by: new FormControl(''),
                id_user_create: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                id_data_flow: new FormControl(''),
                nomor: new FormControl(''),
                status: new FormControl(''),
                posisi: new FormControl(''),

            }
        )
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyPengumumanService.putCompanyPengumumenId({id:this.id_pengumuman, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/pengumuman/list'])
    }

}
